import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';
import InputLabel from '@mui/material/InputLabel';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Link from '@components/link';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';

const Contact = () => {
  const typeId = 'contact__formType';
  const clientnameId = 'contact__formClientname';
  const emailId = 'contact__formEmail';
  const addressId = 'contact__formAddress';
  const inquiryId = 'contact__formInquiry';

  const [forms, setForms] = useState({
    [typeId]: '物件の購入/売却について',
    [clientnameId]: '',
    [emailId]: '',
    [addressId]: '',
    [inquiryId]: ''
  });

  const [isSubmitted, setIsSubmitted] = useState(false);
  const [isCompleted, setIsCompleted] = useState(false);

  const onChangeForm = (key, value) => {
    setForms({
      ...forms,
      [key]: value
    });
  };

  return (
    <Box>
      {isCompleted ? (
        <Box>
          <Typography>
            お問い合わせいただきありがとうございます。
            <br />
            のちほど、担当者よりご連絡いたします。
          </Typography>
        </Box>
      ) : (
        <Box>
          <form
            action='https://docs.google.com/forms/u/0/d/e/1FAIpQLSdmhSJPGg_4o9n_GImOJv9ouIck8ZU9LAkv_uhvtP0oHxmgmg/formResponse'
            method='POST'
            target='onSubmitIframe'
            onSubmit={() => {
              setIsSubmitted(true);
            }}
          >
            {[
              {
                name: 'entry.880012115',
                id: typeId,
                required: true,
                label: 'お問い合わせ種別',
                selects: [
                  '物件の購入/売却について',
                  '物件の管理について',
                  '採用について',
                  'その他'
                ]
              },
              {
                name: 'entry.647326027',
                id: clientnameId,
                required: true,
                label: 'お名前',
                placeholder: '達磨 大師'
              },
              {
                name: 'entry.203767280',
                id: emailId,
                type: 'email',
                required: true,
                label: 'メールアドレス',
                placeholder: 'taishi@daruma-inv.com'
              },
              {
                name: 'entry.635694597',
                id: addressId,
                label: 'ご住所',
                placeholder: '東京都渋谷区千駄ヶ谷2-10-5'
              },
              {
                name: 'entry.1719669779',
                id: inquiryId,
                required: true,
                label: 'お問い合わせ内容',
                placeholder:
                  '不動産の購入・売却、などなどお気軽にお問い合わせください。',
                multiline: true,
                minRows: 5
              }
            ].map(
              (
                {
                  name,
                  id,
                  type = 'text',
                  required,
                  label,
                  placeholder,
                  multiline,
                  minRows,
                  selects
                },
                i
              ) => (
                <Box key={i} mb={8}>
                  <InputLabel
                    htmlFor={id}
                    sx={{
                      color: 'text.primary',
                      mb: 2
                    }}
                  >
                    <Typography
                      component='span'
                      fontSize={10}
                      fontWeight='bold'
                      px={0.5}
                      mr={1}
                      color={required ? 'common.white' : '#333'}
                      bgcolor={required ? 'text.primary' : 'text.secondary'}
                    >
                      {required ? '必須' : '任意'}
                    </Typography>
                    {label}
                  </InputLabel>
                  {selects ? (
                    <Select
                      name={name}
                      onChange={e => {
                        onChangeForm(id, e.target.value);
                      }}
                      value={forms[id]}
                      sx={{ borderRadius: 0 }}
                      fullWidth
                      variant='standard'
                    >
                      {selects.map((d, i) => (
                        <MenuItem key={i} value={d}>
                          {d}
                        </MenuItem>
                      ))}
                    </Select>
                  ) : (
                    <TextField
                      variant='standard'
                      name={name}
                      id={id}
                      fullWidth
                      required={required}
                      type={type}
                      multiline={multiline}
                      minRows={minRows}
                      placeholder={placeholder}
                      value={forms[id]}
                      onChange={e => onChangeForm(id, e.target.value)}
                    />
                  )}
                </Box>
              )
            )}
            <Typography my={3} color='text.primary'>
              <Checkbox required />
              <Link href='/privacy/' target='_blank'>
                プライバシーポリシー
              </Link>{' '}
              に同意します。
            </Typography>
            <Button
              type='submit'
              fullWidth
              sx={{
                height: 80,
                borderRadius: 0,
                bgcolor: 'common.black',
                '& .MuiTypography-root': {
                  color: 'common.white'
                },
                '&:hover': {
                  '& .MuiTypography-root': {
                    color: 'common.black'
                  }
                }
              }}
            >
              <Typography color='#151618'>上記の内容で送信する</Typography>
            </Button>
            <Typography mt={2} color='#707070' variant='body2'>
              ※上のフォームから送信がうまくいかない場合は{' '}
              <Link
                href='https://docs.google.com/forms/d/e/1FAIpQLSdmhSJPGg_4o9n_GImOJv9ouIck8ZU9LAkv_uhvtP0oHxmgmg/viewform'
                target='_blank'
              >
                こちら
                <i className='fas fa-external-link-alt'></i>
              </Link>{' '}
              からお問い合わせください。
            </Typography>
          </form>
        </Box>
      )}
      <iframe
        title='iframe'
        id='onSubmitIframe'
        name='onSubmitIframe'
        style={{ display: 'none' }}
        onLoad={() => {
          if (isSubmitted) {
            // for safe
            setTimeout(() => {
              setIsCompleted(true);
            }, 500);
          }
        }}
      />
    </Box>
  );
};

export default Contact;
